@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Lexend-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Lexend-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Lexend-Black.ttf") format("truetype");
}
[class*=__container] {
  width: min(95%, 120rem);
  margin-inline: auto;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: 400 1.4rem/1.5 "Lexend", sans-serif;
}
@media (min-width: 768px) {
  body {
    font: 400 1.6rem/1.5 "Lexend", sans-serif;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font: 700 4rem/1.2 "Lexend", sans-serif;
}

h2 {
  font: 700 3.2rem/1.2 "Lexend", sans-serif;
}

h3 {
  font: 700 2.4rem/1.2 "Lexend", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

.header {
  padding: 4rem 0 10rem;
  background-image: url("../img/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media screen and (min-width: 768px) {
  .header {
    padding: 4rem 0 16rem;
  }
}
.header__busqueda {
  text-align: center;
}
.header__busqueda-label {
  margin-bottom: 1.6rem;
}
.header__busqueda-boton {
  display: inline-block;
  padding: 0.8rem 1.6rem;
  border-radius: 3rem;
  font-size: 1.6rem;
  font-weight: 700;
  color: #d1222e;
  background-color: #FFFFFF;
}

.barra {
  display: none;
}
@media screen and (min-width: 768px) {
  .barra {
    margin-bottom: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media screen and (min-width: 1024px) {
  .barra {
    margin-bottom: 8rem;
  }
}
.barra__menu {
  display: flex;
  gap: 2.4rem;
}
@media screen and (min-width: 1024px) {
  .barra__menu {
    gap: 4rem;
  }
}
.barra__logo {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 900;
  text-shadow: 6px 6px 2px #000;
}
.barra__link {
  color: #FFFFFF;
  text-shadow: 0px 1px 12px #000;
}

.formulario {
  max-width: 50rem;
  margin-inline: auto;
  margin-bottom: 10rem;
  border-radius: 4rem;
  padding: 2.4rem;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  background-color: #FFFFFF;
}
@media screen and (min-width: 768px) {
  .formulario {
    max-width: 100rem;
    margin-bottom: 12rem;
    border-radius: 5rem;
    padding: 2.4rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0.8rem;
    box-shadow: 0px 3px 7px #686868;
  }
}
@media screen and (min-width: 1024px) {
  .formulario {
    margin-bottom: 16rem;
  }
}
.formulario__label {
  display: block;
}
@media screen and (min-width: 768px) {
  .formulario__label {
    font-size: 1.2rem;
    font-weight: 900;
  }
}
.formulario__input {
  width: 100%;
  padding: 0.4rem 0.4rem 0.4rem 1.2rem;
  border-radius: 5rem;
  border: 1px solid #c7c7c7;
}
.formulario__input::placeholder {
  font-size: 1.2rem;
}
.formulario__boton {
  width: 100%;
  border: none;
  border-radius: 4rem;
  padding: 0.8rem 1.6rem;
  align-self: flex-end;
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;
  background-image: radial-gradient(circle, rgb(255, 56, 92) 0%, #e31f2d 100%);
}
@media screen and (min-width: 768px) {
  .formulario__boton {
    width: auto;
    padding-inline: 3.6rem;
  }
}

@supports (background-image: url("../img/header.webp")) {
  .header {
    background-image: url("../img/header.webp");
  }
}
@supports (background-image: url("../img/header.avif")) {
  .header {
    background-image: url("../img/header.avif");
  }
}
.destinos {
  padding-block: 2.4rem;
}
@media (min-width: 1024px) {
  .destinos {
    padding-block: 4.8rem;
  }
}
.destinos__titulo {
  margin-bottom: 2.4rem;
}
@media (min-width: 1024px) {
  .destinos__titulo {
    margin-bottom: 4.8rem;
  }
}
.destinos__grid {
  max-width: 50rem;
  margin-inline: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .destinos__grid {
    max-width: unset;
    grid-template-columns: repeat(4, 1fr);
  }
}
.destinos__destino {
  text-align: center;
}
.destinos__img {
  width: 15rem;
  height: 15rem;
  margin-inline: auto;
}

.lugares {
  padding-block: 2.4rem;
}
@media (min-width: 1024px) {
  .lugares {
    padding-block: 4.8rem;
  }
}
.lugares__titulo {
  margin-bottom: 2.4rem;
}
@media (min-width: 1024px) {
  .lugares__titulo {
    margin-bottom: 4.8rem;
  }
}
.lugares__grid {
  overflow-x: scroll;
  padding-bottom: 1.6rem;
  display: flex;
  gap: 1.6rem;
  scroll-snap-type: x mandatory;
}
@media (min-width: 768px) {
  .lugares__grid {
    overflow-x: unset;
    scroll-snap-type: unset;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
.lugares__grid::-webkit-scrollbar {
  width: 100%;
  height: 0.8rem;
}
.lugares__grid::-webkit-scrollbar-track {
  background: #c7c7c7;
}
.lugares__grid::-webkit-scrollbar-thumb {
  background-color: #d1222e;
  border-radius: 1rem;
}
.lugares__lugar {
  scroll-snap-align: start;
}
.lugares__bg {
  width: 30rem;
  height: 30rem;
  margin-bottom: 0.8rem;
  border-radius: 1.6rem;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .lugares__bg {
    width: 100%;
  }
}

.lugares__bg--1 {
  background-image: url("../img/vive_1.jpg");
}

@supports (background-image: url("../img/vive_1.webp")) {
  .lugares__bg--1 {
    background-image: url("../img/vive_1.webp");
  }
}
@supports (background-image: url("../img/vive_1.avif")) {
  .lugares__bg--1 {
    background-image: url("../img/vive_1.avif");
  }
}
.lugares__bg--2 {
  background-image: url("../img/vive_2.jpg");
}

@supports (background-image: url("../img/vive_2.webp")) {
  .lugares__bg--2 {
    background-image: url("../img/vive_2.webp");
  }
}
@supports (background-image: url("../img/vive_2.avif")) {
  .lugares__bg--2 {
    background-image: url("../img/vive_2.avif");
  }
}
.lugares__bg--3 {
  background-image: url("../img/vive_3.jpg");
}

@supports (background-image: url("../img/vive_3.webp")) {
  .lugares__bg--3 {
    background-image: url("../img/vive_3.webp");
  }
}
@supports (background-image: url("../img/vive_3.avif")) {
  .lugares__bg--3 {
    background-image: url("../img/vive_3.avif");
  }
}
.lugares__bg--4 {
  background-image: url("../img/vive_4.jpg");
}

@supports (background-image: url("../img/vive_4.webp")) {
  .lugares__bg--4 {
    background-image: url("../img/vive_4.webp");
  }
}
@supports (background-image: url("../img/vive_4.avif")) {
  .lugares__bg--4 {
    background-image: url("../img/vive_4.avif");
  }
}
.anfitrion {
  min-height: 65rem;
  padding: 5rem;
  border-radius: 1.6rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 10rem;
  color: #FFFFFF;
  background-size: 100%, 60rem;
  background-position: center, center bottom;
  background-repeat: no-repeat;
  background-size: 100%, 58rem;
}
@media (min-width: 579px) {
  .anfitrion {
    min-height: 75rem;
    background-size: 100%, 75rem;
    background-position: center, center 35rem;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    min-height: unset;
    grid-template-rows: unset;
    background-size: 100%, auto;
    background-position: center, 50% 30%;
    padding: 15rem 5rem;
  }
}
@media (min-width: 768px) {
  .anfitrion__contenido {
    max-width: 45%;
  }
}
.anfitrion__titulo {
  margin-bottom: 2.4rem;
}
.anfitrion__texto {
  margin-bottom: 2.4rem;
}
.anfitrion__enlace {
  padding: 0.8rem 1.6rem;
  border-radius: 20rem;
  font-weight: 700;
  font-size: 1.6rem;
  display: inline-block;
  color: #000;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .anfitrion__enlace {
    padding: 1.6rem 2.4rem;
  }
}

.anfitrion {
  background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 50%), url("../img/anfitrion.jpg");
}
@media (min-width: 768px) {
  .anfitrion {
    background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 55%), url("../img/anfitrion.jpg");
  }
}

@supports (background-image: url("../img/anfitrion.webp")) {
  .anfitrion {
    background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 50%), url("../img/anfitrion.webp");
  }
  @media (min-width: 768px) {
    .anfitrion {
      background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 55%), url("../img/anfitrion.webp");
    }
  }
}
@supports (background-image: url("../img/anfitrion.avif")) {
  .anfitrion {
    background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 50%), url("../img/anfitrion.avif");
  }
  @media (min-width: 768px) {
    .anfitrion {
      background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 55%), url("../img/anfitrion.avif");
    }
  }
}
.experiencias {
  padding-block: 2.4rem;
}
@media (min-width: 1024px) {
  .experiencias {
    padding-block: 4.8rem;
  }
}
.experiencias__titulo {
  margin-bottom: 0.8rem;
}
@media (min-width: 1024px) {
  .experiencias__titulo {
    margin-bottom: 1.6rem;
  }
}
.experiencias__descripcion {
  margin-bottom: 2.4rem;
}
@media (min-width: 768px) {
  .experiencias__descripcion {
    margin-bottom: 4.8rem;
  }
}
.experiencias__grid {
  overflow-x: scroll;
  padding-bottom: 1.6rem;
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  gap: 1.6rem;
  scroll-snap-type: x mandatory;
}
@media (min-width: 768px) {
  .experiencias__grid {
    overflow-x: unset;
    scroll-snap-type: unset;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.experiencias__grid::-webkit-scrollbar {
  width: 100%;
  height: 0.8rem;
}
.experiencias__grid::-webkit-scrollbar-track {
  background: #c7c7c7;
}
.experiencias__grid::-webkit-scrollbar-thumb {
  background-color: #d1222e;
  border-radius: 1rem;
}
.experiencias__experiencia {
  scroll-snap-align: start;
}
.experiencias__img {
  width: 30rem;
  height: 30rem;
  border-radius: 1.6rem;
  margin-bottom: 0.8rem;
}
@media (min-width: 768px) {
  .experiencias__img {
    width: 100%;
    height: auto;
  }
}
.experiencias__subtitulo {
  font-weight: 700;
}

@media (min-width: 768px) {
  .footer {
    padding-top: 4.8rem;
  }
}
.footer__grid {
  margin-bottom: 2.4rem;
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__titulo {
  font-weight: 700;
}
.footer__menu {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.footer__link {
  color: #686868;
}
.footer__barra {
  padding-block: 1.6rem;
  border-top: 1px solid #c7c7c7;
}
.footer__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
}
.footer__link-barra::after {
  content: "-";
  margin-left: 0.8rem;
  display: inline-block;
}
.footer__link-barra:last-child::after {
  content: "";
}